import {getStyle} from "../../config/mUtils";

export const loadMore = {
  directives: {
    'load-more': {
      bind:(el,binding) => {
        let windowHeight = window.screen.height;
        let scrollEl = document.body;               // ()document.body
        let heightEl = el;                          // 
        let height;
        let setTop;
        let paddingBottom;
        let marginBottom;
        let oldScroll;
        let requestFram;


        document.body.addEventListener('scroll',() => {
          loadStart();
        }, false);

        //   offseTop, pading, margin
        el.addEventListener('touchstart',() => {
          height = heightEl.clientHeight;
          setTop = el.offsetTop;
          paddingBottom = getStyle(el,'paddingBottom');
          marginBottom = getStyle(el,'marginBottom');
        },{passive: true});
        // scrollTop 
        el.addEventListener('touchmove',() => {
          loadStart();
        },{passive: true});
        //
        el.addEventListener('touchend',() => {
          oldScroll = scrollEl.scrollTop;
          moveEnd();
        },{passive: true});

        const moveEnd = () => {
          requestFram = requestAnimationFrame(() => {
            if (scrollEl.scrollTop != oldScroll){
              oldScroll = scrollEl.scrollTop;
              moveEnd();
            }else {
              cancelAnimationFrame(requestFram);
              //dom
              height = heightEl.clientHeight;
              loadStart();
            }
          });
        };

        const loadStart = () => {
          if (scrollEl.scrollTop + windowHeight >= height + setTop + paddingBottom + marginBottom) {
            binding.value();
          }
        }

      }
    }
  }
};

export const getImgPath = {
  methods: {
    //
    getImgPath(path) {
      let suffix;
      if (!path) {
        return '//elm.cangdu.org/img/default.jpg'
      }
      if (path.indexOf('jpeg') !== -1) {
        suffix = '.jpeg'
      } else {
        suffix = '.png'
      }
      let url = '/' + path.substr(0, 1) + '/' + path.substr(1, 2) + '/' + path.substr(3) + suffix;
      return 'https://fuss10.elemecdn.com' + url
    },
  }

};



